
html,
body {
  margin: 0;
  font-family: 'Avenir', sans-serif;
}
.streetviewcontainer {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  height: 100vh;
  width: 50vw;
}
#streetview {
  height: 100vh;
  width: 50vw;
}
.gm-control-active,
.gm-iv-address,
.gm-style-cc,
.gmnoprint,
#streetview img,
.gmnoscreen {
  display: none;
}
.right {
  height: 100vh;
  position: absolute;
  width: 50vw;
  left: 50%;
  top: 0;
  display: grid;
  grid-template-rows: 80px 1fr;
}
.right .righttop {
  background: floralwhite;
  color: #444;
  position: relative;
  text-align: center;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: stretch;
      -ms-flex-pack: stretch;
          justify-content: stretch;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 1.8rem;
}
.right .righttop > * {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.highlight {
  color: green;
  font-weight: 600;
}
button {
  height: 100%;
  border: none;
  font-size: 1em;
  padding: 0 30px;
  background: green;
  color: white;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  color: white;
  outline: 0;
}
button:hover {
  background: darkgreen;
  cursor: pointer;
}
button.secondary {
  background: gray;
}







































#map {
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: opacity 3s;
  transition: opacity 3s;
}
#map,
canvas {
  outline: none !important;
}
#map.ready {
  opacity: 1;
}
#map.focus {
  outline: 0;
}
.mapbox-ctrl-attrib,
.mapboxgl-missing-css,
.mapboxgl-control-container {
  display: none;
}
.mapboxgl-marker {
  position: absolute;
  z-index: 2;
}
